import ReactDOM from "react-dom"
// import Remark from "remark-slide/out/remark.min"
import { React } from "src/utils/view"

import { PostFrontmatter } from "src/utils/types"

type Props = {
  frontmatter: PostFrontmatter
  html: string
}

declare var window: any

export default class FullSlide extends React.PureComponent<Props, {}> {
  componentDidMount() {
    if (typeof window !== "undefined") {
      const s = window.document.createElement("script")
      s.type = "text/javascript"
      s.async = true
      s.src = "/remark-latest.min.js"
      s.addEventListener("load", () => {
        if (window["remark"]) {
          window["remark"].create()
        }
      })
      const x = document.getElementsByTagName("body")[0]
      x.appendChild(s)
    }
  }

  render() {
    return (
      typeof window !== "undefined" &&
      ReactDOM.createPortal(
        <>
          <textarea id="source" value={this.props.html} readOnly />
        </>,
        document.getElementsByTagName("body")[0]
      )
    )
  }
}
